:root {
    /* primary */
    --link-primary-color: var(--color-light-text-primary);
    --link-primary-hover-color: var(--color-light-text-primary-shade-20);
    --link-primary-active-color: var(--color-light-text-primary-shade-40);

    /* secondary */
    --link-secondary-color: var(--color-light-text-secondary);
    --link-secondary-hover-color: var(--color-light-text-secondary-shade-20);
    --link-secondary-active-color: var(--color-light-text-secondary-shade-40);

    /* default */
    --link-default-color: var(--color-light-text-link);
    --link-default-hover-color: var(--color-light-text-link-shade-20);
    --link-default-active-color: var(--color-light-text-link-shade-40);
}
