    /* Hard up */
}
:root {
    --form-control-border-bottom: 0;
    --form-control-bg-color: var(--color-light-specialbg-tertiary-transparent);
    --form-control-color: var(--color-light-text-primary);
    --form-control-label-color: var(--color-light-text-secondary);
    --form-control-hint-color: var(--color-light-text-secondary);

    /* disabled */
    --form-control-disabled-bg-color: var(--color-light-specialbg-secondary-transparent);
    --form-control-disabled-border-bottom: 0;
    --form-control-disabled-color: var(--color-light-text-secondary);
    --form-control-disabled-filled-label-color: var(--form-control-disabled-color);
    --form-control-disabled-shadow: none;

    /* focus */
    --form-control-focus-shadow: inset 0 0 0 2px var(--color-light-graphic-secondary);
    --form-control-focus-error-shadow: inset 0 0 0 2px var(--color-light-graphic-negative);
    --form-control-focus-border-bottom: 0;
    --form-control-focus-bg-color: var(--color-light-bg-primary);

    /* hover */
    --form-control-hover-bg-color: var(--color-light-specialbg-secondary-transparent);

    /* error */
    --form-control-error-color: var(--color-light-text-negative);
    --form-control-error-label-color: var(--color-light-text-secondary);
    --form-control-error-shadow: inset 0 0 0 2px var(--color-light-graphic-negative);
    --form-control-error-disabled-shadow: var(--form-control-error-shadow);
    --form-control-error-border-bottom: 0;
    --form-control-error-bg-color: var(--color-light-specialbg-secondary-transparent);

