}
.form-control__component_tcjks {
    color: var(--form-control-color);
}
.form-control__inner_tcjks {
    background-color: var(--form-control-bg-color);
}
/* Нижняя граница */
.form-control__inner_tcjks:before {
    border-bottom: var(--form-control-border-bottom);
}
.form-control__label_tcjks {
    color: var(--form-control-label-color);
}
.form-control__hint_tcjks {
    color: var(--form-control-hint-color);
}
/* DISABLED STATE */
.form-control__disabled_tcjks {
    background-color: var(--form-control-disabled-bg-color);
    color: var(--form-control-disabled-color);
    box-shadow: var(--form-control-disabled-shadow);
}
.form-control__disabled_tcjks.form-control__hasError_tcjks {
    box-shadow: var(--form-control-error-disabled-shadow);
    background-color: var(--form-control-disabled-bg-color);
}
.form-control__disabled_tcjks:before {
    border-bottom: var(--form-control-disabled-border-bottom);
}
.form-control__disabled_tcjks.form-control__filled_tcjks .form-control__label_tcjks {
    color: var(--form-control-disabled-filled-label-color);
}
/* HOVER STATE */
.form-control__inner_tcjks:not(.form-control__disabled_tcjks):hover {
    background-color: var(--form-control-hover-bg-color);
}
/* FOCUS STATE */
.form-control__focused_tcjks:not(.form-control__disabled_tcjks):not(.form-control__hasError_tcjks):before {
    border-bottom: var(--form-control-focus-border-bottom);
}
.form-control__focused_tcjks.form-control__inner_tcjks,
.form-control__focused_tcjks.form-control__inner_tcjks:hover {
    background-color: var(--form-control-focus-bg-color);
    box-shadow: var(--form-control-focus-shadow)
}
.form-control__focused_tcjks.form-control__inner_tcjks.form-control__hasError_tcjks, .form-control__focused_tcjks.form-control__inner_tcjks:hover.form-control__hasError_tcjks {
        box-shadow: var(--form-control-focus-error-shadow);
    }
/* ERROR STATE */
.form-control__error_tcjks {
    color: var(--form-control-error-color);
}
.form-control__hasError_tcjks .form-control__label_tcjks {
    color: var(--form-control-error-label-color);
}
.form-control__hasError_tcjks:before {
    border-bottom: var(--form-control-error-border-bottom);
}
.form-control__hasError_tcjks {
    box-shadow: var(--form-control-error-shadow);
    background-color: var(--form-control-error-bg-color);
}
