:root {

    /* options list */
    --select-options-list-empty-placeholder-color: var(--color-light-text-secondary);
    --select-option-divider-display: none;
    --select-option-divider-background: var(--color-light-border-primary);

    /* option */

    --select-option-left-padding: var(--gap-s);
    --select-option-right-padding: var(--gap-s);

    /* checkmark */

    /* optgroup */
}
.select__virtualOptionsList_bkmps {
    width: 100%;
    outline: none;
    box-sizing: border-box;
    position: relative;
}
.select__scrollable_bkmps {
    overflow: auto;
    width: 100%;
}
.select__inner_bkmps {
    position: relative;
    width: 100%;
}
.select__virtualRow_bkmps {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%
}
.select__virtualRow_bkmps:before {
        content: '';
        position: absolute;
        z-index: 1;
        left: var(--select-option-left-padding);
        right: var(--select-option-right-padding);
        top: 0;
        height: 1px;
        background: var(--select-option-divider-background);
        display: var(--select-option-divider-display);
    }
.select__virtualRow_bkmps:first-child:before {
        display: none;
    }
.select__highlighted_bkmps:before,
    .select__highlighted_bkmps + .select__virtualRow_bkmps:before {
        display: none;
    }
.select__emptyPlaceholder_bkmps {
    padding: var(--gap-m) var(--gap-s);
    color: var(--select-options-list-empty-placeholder-color);
}
.select__l_bkmps .select__emptyPlaceholder_bkmps,
.select__xl_bkmps .select__emptyPlaceholder_bkmps {
    padding: var(--gap-xl) var(--gap-m);
}
