:root {
    --color-static-graphic-solitude: #e8f2fe;
}
:root {
    --color-light-graphic-primary: #0b1f35;
    --color-light-graphic-primary-inverted: #fff;
    --color-light-graphic-quaternary: #dbdee1;
    --color-light-specialbg-component: rgba(11, 31, 53, 0.07);
    --color-light-specialbg-nulled: rgba(255, 255, 255, 0);
    --color-light-text-accent: #ef3124;
    --color-light-text-link: #007aff;
    --color-light-text-primary: #0b1f35;
    --color-light-text-primary-inverted: #fff;
    --color-light-text-secondary: rgba(11, 31, 53, 0.7);
    --color-light-text-tertiary: rgba(11, 31, 53, 0.3);
    --color-light-graphic-primary-tint-15: rgb(48, 65, 83);
}
:root {

    /* Hard */

    /* Up */

    /* Hard up */
}
:root {
    --gap-2xs: 4px;
}
:root {
    --border-radius-circle: 50%;
    --border-radius-pill: 99px;
}
:root {

    /* Кнопки выбора месяцев и годов */

    /* Шапка */

    /* День */
    --calendar-day-size: 40px;
    --calendar-day-border-radius: var(--border-radius-pill);

    /* today */
    --calendar-today-color: var(--color-light-text-link);

    /* highlighted */
    --calendar-highlighted-color: var(--color-light-text-primary);
    --calendar-highlighted-background: var(--color-light-graphic-quaternary);

    /* holidays */
    --calendar-holiday-color: var(--color-light-text-accent);

    /* range */
    --calendar-range-color: var(--color-light-text-primary);
    --calendar-range-background: var(--color-light-specialbg-component);
    --calendar-range-complete-background: var(--color-static-graphic-solitude);

    /* selected */
    --calendar-selected-color: var(--color-light-text-primary-inverted);
    --calendar-selected-background: var(--color-light-graphic-primary);
    --calendar-selected-hover-background: var(--color-light-graphic-primary-tint-15);

    /* disabled */
    --calendar-disabled-color: var(--color-light-text-tertiary);
    --calendar-disabled-background: transparent;

    /* marker */
    --calendar-marker-background: var(--color-light-graphic-primary);
    --calendar-marker-selected-background: var(--color-light-graphic-primary-inverted);
}
.calendar__daysTable_kukl8 {
    border-collapse: separate;
    border-spacing: 0;
    position: relative
}
.calendar__daysTable_kukl8.calendar__responsive_kukl8 {
        width: 100%;
    }
.calendar__daysTable_kukl8 th {
        padding: 0;
    }
.calendar__daysTable_kukl8 td {
        padding: var(--gap-2xs) 0 0;
    }
.calendar__dayName_kukl8 {
    font-size: 14px;
    line-height: 20px;
    font-weight: 400;

    height: var(--calendar-day-size);
    color: var(--color-light-text-secondary)
}
@media (max-width: 374px) {
.calendar__dayName_kukl8 {
        height: 36px
}
    }
.calendar__day_kukl8 {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    position: relative;
    width: var(--calendar-day-size);
    height: var(--calendar-day-size);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    box-sizing: border-box;
    border-radius: var(--calendar-day-border-radius);
    color: var(--color-light-text-primary);
    z-index: 1
}
@media (max-width: 374px) {
.calendar__day_kukl8 {
        width: 36px;
        height: 36px
}
    }
.calendar__day_kukl8.calendar__highlighted_kukl8,
    .calendar__day_kukl8.calendar__range_kukl8.calendar__highlighted_kukl8:not(.calendar__disabled_kukl8) {
        color: var(--calendar-highlighted-color);
        background: var(--calendar-highlighted-background);
    }
.calendar__day_kukl8.calendar__holiday_kukl8 {
        color: var(--calendar-holiday-color);
    }
.calendar__day_kukl8.calendar__disabled_kukl8 {
        pointer-events: none;
        color: var(--calendar-disabled-color);

        background: var(--calendar-disabled-background);
        cursor: default;
    }
.calendar__day_kukl8.calendar__today_kukl8 {
        font-weight: bold;
        color: var(--calendar-today-color);
    }
.calendar__day_kukl8.calendar__rangeComplete_kukl8 {
        background: var(--calendar-range-complete-background);
    }
.calendar__day_kukl8.calendar__selected_kukl8,
    .calendar__day_kukl8.calendar__disabled_kukl8.calendar__selected_kukl8 {
        color: var(--calendar-selected-color);
        background: var(--calendar-selected-background);
    }
@media (hover) {
            .calendar__day_kukl8.calendar__selected_kukl8:hover {
                background: var(--calendar-selected-hover-background);
            }
        }
.calendar__day_kukl8:focus {
        z-index: 1;
    }
/* RANGE */
.calendar__dayWrapper_kukl8 {
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    position: relative
}
.calendar__dayWrapper_kukl8:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: var(--calendar-day-size);
    }
.calendar__dayWrapper_kukl8:focus {
        outline: none;
    }
.calendar__dayWrapper_kukl8.calendar__cursorPointer_kukl8 {
        cursor: pointer;
    }
.calendar__range_kukl8 .calendar__day_kukl8:not(.calendar__selected_kukl8):not(.calendar__disabled_kukl8):not(.calendar__today_kukl8):not(.calendar__holiday_kukl8) {
        color: var(--calendar-range-color);
    }
.calendar__range_kukl8:before {
        background: var(--calendar-range-background);
    }
.calendar__rangeEnd_kukl8:before {
        width: calc(50% + 20px);
        border-top-right-radius: var(--calendar-day-border-radius);
        border-bottom-right-radius: var(--calendar-day-border-radius);
    }
.calendar__rangeStart_kukl8:before {
        left: auto;
        right: 0;
        width: calc(50% + 20px);
        border-top-left-radius: var(--calendar-day-border-radius);
        border-bottom-left-radius: var(--calendar-day-border-radius);
    }
.calendar__rangeComplete_kukl8:before {
        background: var(--calendar-range-complete-background);
    }
.calendar__transitLeft_kukl8:after, .calendar__transitRight_kukl8:after {
        content: '';
        position: absolute;
        display: block;
        width: 100%;
        height: var(--calendar-day-size);
        bottom: 0;
        pointer-events: none;
    }
.calendar__transitLeft_kukl8:after {
        right: 100%;
        background: linear-gradient(
            270deg,
            var(--calendar-range-background) 0%,
            var(--color-light-specialbg-nulled) 100%
        );
    }
.calendar__transitLeft_kukl8:first-child:before {
            opacity: 0;
        }
.calendar__transitLeft_kukl8:first-child:after {
            right: 0;
        }
.calendar__transitRight_kukl8:after {
        left: 100%;
        background: linear-gradient(
            270deg,
            var(--color-light-specialbg-nulled) 0%,
            var(--calendar-range-background) 100%
        );
    }
.calendar__transitRight_kukl8:last-child:before {
            opacity: 0;
        }
.calendar__transitRight_kukl8:last-child:after {
            left: 0;
        }
.calendar__sharpTransitLeft_kukl8:first-child:before {
            opacity: 1;
        }
.calendar__sharpTransitLeft_kukl8:first-child:after {
            opacity: 0;
        }
.calendar__sharpTransitRight_kukl8:last-child:before {
            opacity: 1;
        }
.calendar__sharpTransitRight_kukl8:last-child:after {
            opacity: 0;
        }
.calendar__rangeComplete_kukl8.calendar__transitLeft_kukl8:after {
        background: linear-gradient(
            270deg,
            var(--calendar-range-complete-background) 0%,
            var(--color-light-specialbg-nulled) 100%
        );
    }
.calendar__rangeComplete_kukl8.calendar__transitRight_kukl8:after {
        background: linear-gradient(
            270deg,
            var(--color-light-specialbg-nulled) 0%,
            var(--calendar-range-complete-background) 100%
        );
    }
/* SLIDE ANIMATION */
.calendar__daysEnter_kukl8 .calendar__dayWrapper_kukl8 {
    width: 100px;
}
.calendar__left_kukl8 .calendar__daysEnter_kukl8 {
        transform: translateX(50px);
    }
.calendar__left_kukl8 .calendar__daysEnterActive_kukl8 {
        transform: translateX(0);
    }
.calendar__left_kukl8 .calendar__daysExit_kukl8 {
        transform: translateX(0);
    }
.calendar__left_kukl8 .calendar__daysExitActive_kukl8 {
        transform: translateX(-50px);
    }
.calendar__right_kukl8 .calendar__daysEnter_kukl8 {
        transform: translateX(-50px);
    }
.calendar__right_kukl8 .calendar__daysEnterActive_kukl8 {
        transform: translateX(0);
    }
.calendar__right_kukl8 .calendar__daysExit_kukl8 {
        transform: translateX(0);
    }
.calendar__right_kukl8 .calendar__daysExitActive_kukl8 {
        transform: translateX(50px);
    }
.calendar__dot_kukl8 {
    display: block;
    position: absolute;
    bottom: 4px;
    width: 4px;
    height: 4px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: var(--border-radius-circle);
    background: var(--calendar-marker-background);
}
.calendar__selected_kukl8 .calendar__dot_kukl8 {
    background: var(--calendar-marker-selected-background);
}
.calendar__daysEnter_kukl8 {
    opacity: 0;
    position: absolute;
    left: 0;
}
.calendar__daysEnterActive_kukl8 {
    opacity: 1;
}
.calendar__daysExit_kukl8 {
    pointer-events: none;
    opacity: 1;
}
.calendar__daysExitActive_kukl8 {
    opacity: 0;
}
.calendar__daysEnterActive_kukl8,
.calendar__daysExitActive_kukl8 {
    transition: opacity 200ms, transform 300ms;
}
