.modal__appear_101ev,
.modal__enter_101ev {
    opacity: 0;
    transform: translateY(15px);
}

.modal__appearActive_101ev,
.modal__enterActive_101ev {
    opacity: 1;
    transform: translateY(0);
    transition: opacity 200ms ease-in, transform 200ms ease-in;
}

.modal__exit_101ev {
    opacity: 1;
    transform: translateY(0);
}

.modal__exitActive_101ev,
.modal__exitDone_101ev {
    opacity: 0;
    transform: translateY(15px);
    transition: opacity 200ms ease-out, transform 200ms ease-out;
}
