:root {
    /* primary */
    --action-button-inverted-primary-base-color: var(--color-light-text-primary-inverted);
    --action-button-inverted-primary-base-icon-color: var(--color-light-graphic-primary);
    --action-button-inverted-primary-base-bg-color: var(--color-light-bg-primary);
    --action-button-inverted-primary-hover-color: var(--color-light-bg-primary-shade-7);
    --action-button-inverted-primary-active-color: var(--color-light-bg-primary-shade-15);

    /* secondary */
    --action-button-inverted-secondary-base-color: var(--color-light-text-primary-inverted);
    --action-button-inverted-secondary-base-icon-color: var(--color-light-graphic-primary-inverted);
    --action-button-inverted-secondary-base-bg-color: var(--color-light-bg-secondary-inverted);
    --action-button-inverted-secondary-hover-color: var(
        --color-light-bg-secondary-inverted-tint-15
    );
    --action-button-inverted-secondary-active-color: var(
        --color-light-bg-secondary-inverted-tint-20
    );
}
.action-button__primary_15z00 {
    color: var(--action-button-inverted-primary-base-color)
}
.action-button__primary_15z00 .action-button__iconWrapper_15z00 {
        color: var(--action-button-inverted-primary-base-icon-color);
        background-color: var(--action-button-inverted-primary-base-bg-color);
    }
.action-button__primary_15z00:hover .action-button__iconWrapper_15z00 {
        background-color: var(--action-button-inverted-primary-hover-color);
    }
.action-button__primary_15z00:active .action-button__iconWrapper_15z00 {
        background-color: var(--action-button-inverted-primary-active-color);
    }
.action-button__secondary_15z00 {
    color: var(--action-button-inverted-secondary-base-color)
}
.action-button__secondary_15z00 .action-button__iconWrapper_15z00 {
        color: var(--action-button-inverted-secondary-base-icon-color);
        background-color: var(--action-button-inverted-secondary-base-bg-color);
    }
.action-button__secondary_15z00:hover .action-button__iconWrapper_15z00 {
        background-color: var(--action-button-inverted-secondary-hover-color);
    }
.action-button__secondary_15z00:active .action-button__iconWrapper_15z00 {
        background-color: var(--action-button-inverted-secondary-active-color);
